.main {
  padding: 10px 100px 30px 100px;
  display: flex;
  justify-content: center;
}

.mainWrapper {
  display: flex;
  flex-direction: column;

  max-width: 975px;
  gap: 20px;
}

.item {
  display: flex;
  justify-content: space-between;

  gap: 20px;

  background-color: var(--color-white);
}

.item:nth-child(even) {
  flex-direction: row-reverse;

  .itemInfo {
    padding: 25px 35px 25px 0;
  }
}

.itemInfo {
  display: flex;
  flex-direction: column;

  padding: 25px 0 25px 35px;
}

.itemTitle,
.itemTimeline,
.itemDescription,
.itemSize,
.itemCapacity,
.itemSquare {
  font-family: 'Helvetica', sans-serif;
}

.itemType,
.itemAddress,
.itemTimeline,
.itemDescription,
.itemSize,
.itemCapacity,
.itemSquare {
  font-family: 'montserrat', sans-serif;
  font-weight: lighter;
  font-size: 17px;
}

.itemType {
  font-size: 13px;
}

.itemTitle {
  font-size: 22px;

  margin: 10px 0 8px 0;
}

.itemAddress {
  line-height: 30px;
  font-size: 17px;
}

.itemSeparator {
  background-color: var(--color-black);

  margin: 25px 0 30px 0;
  width: 75px;
  height: 2px;
}

.itemType,
.itemTitle,
.itemAddress {
  font-weight: bold;
}

.itemTimeline {
  margin: 0 0 25px 0;
}

.itemDescription {
  margin: 0 0 25px 0;
}

.itemPicture {
  height: 100%;
  object-fit: cover;
}